.parent-add{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-evenly;
}

label{height: 25%;}

.bookInput{
    width: 100%;
    margin-top: 7px;
    border-radius: 3px;
    border: 0.5px solid rgb(77, 72, 72);
    height: 35%;
}

.btn-add{
    width: 70%;
    height: 30px;
    margin: auto;
    font-size: 17px;
    font-family: monospace;
    background-color: #fab12a;
    border: 0.5px solid lightgray;
    border-radius: 4px;
    color: white;
    cursor: pointer;
    transition: 300ms;
    margin-top: 0;
}

.btn-add:hover{
    color: #fab12a;
    background-color: white;
}

.head-parent{
    border-top: 0.5px solid black;
    display: flex;
    justify-content: space-evenly;
    border-bottom: 0.5px solid black;
    height: 45px;
    align-items: center;
}
.headTitle{
    font-weight: bold;
    width: 20%;
    display: flex;
    justify-content: center;  
    font-size: 18px;
}

.container-inputs{
    width: 100%;
    height: 340px;
    display: flex;
    flex-direction: column;
}